.frame {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 120px;
  height: 184px;
}

.counter {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
}

.disabled {
  background-color: inherit;
  cursor: not-allowed;
  color: rgba(0, 0, 0, 0.2);
}
.disabled:hover {
  background-color: #f8f8f8;
  transition: ease-in-out 0.2s;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.decrement,
.increment,
.disabled,
.count {
  width: 40px;
  height: 40px;
  border: none;
  font-size: 24px;
}
.decrement,
.increment,
.count {
  background-color: inherit;
  line-height: 40px;
}

.increment:hover,
.decrement:hover {
  background-color: #f7f7f7;
  transition: ease-in-out 0.2s;
  cursor: pointer;
}

.decrement:hover {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.increment:hover {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.increment:active,
.decrement:active {
  background-color: #dcdcdc;
}

.count {
  text-align: center;
}
